  // 
  formatTime(
    date: string | number | Date = '' /*  */,
    fmt = 'YYYY-MM-DD' /*YYYY-MM-DD hh:mm:ss */
  ) {
    //ios
    if (typeof date === 'string') {
      date = date.replace(/-/g, '/');
    }
    date = date ? new Date(date) : new Date();
    var o = {
      'Y+': date.getFullYear(), //
      'M+': date.getMonth() + 1, //
      'D+': date.getDate(), //
      'h+': date.getHours(), //
      'm+': date.getMinutes(), //
      's+': date.getSeconds(), //
      'q+': Math.floor((date.getMonth() + 3) / 3), //
      S: date.getMilliseconds(), //
    };
    if (/(Y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (o['Y+'] + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
  timeFromNow(time) {
    //
    const timestamp = Date.now();
    const createTime = new Date(time);
    time = createTime.getTime();
    const diff = timestamp - time;
    if (diff < 60 * 1000) {
      return '';
    } else if (diff < 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 1000)) + '';
    } else if (diff < 24 * 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 60 * 1000)) + '';
    } else {
      return this.formatTime(time, 'YYYY-MM-DD hh:mm');
    }
  }
  windowActive() {
    var hiddenProperty =
      'hidden' in document
        ? 'hidden'
        : 'webkitHidden' in document
        ? 'webkitHidden'
        : 'mozHidden' in document
        ? 'mozHidden'
        : null;
    var visibilityChangeEvent = hiddenProperty.replace(
      /hidden/i,
      'visibilitychange'
    );
    var onVisibilityChange = function () {
      if (document[hiddenProperty]) {
        // -
        this.isWindowActive = true;
      } else {
        // '
        this.isWindowActive = false;
      }
    };
    document.addEventListener(visibilityChangeEvent, onVisibilityChange);
    window.onblur = () => {
      this.isWindowActive = false;
    };
    window.onfocus = () => {
      this.isWindowActive = true;
    };
  }
  showAlert(msg) {
    this.alert.show = true;
    this.alert.msg = msg;
    setTimeout(() => {
      this.alert.show = false;
    }, 2000);
  }

  doHttpResponse(res, success, fail?) {
    let code = res.code;
    if (code) {
      if (code === 3 || code === 6) {
        // 
        this.isLogin = false;
        this.showAlert('');
        setTimeout(() => {
          this.router.navigate(['/auth/login']);
        }, 2000);
      } else {
        fail ? fail(res) : this.showAlert(res.msg);
      }
    } else {
      // 
      success && success(res.data);
    }
  }
  // 
  sendMsg(msg, success?, fail?) {
    this.http
      .post(this.api.message, msg)
      .pipe(
        catchError(
          this.handleError('sendMsg', () => {
            fail && fail();
          })
        )
      )
      .subscribe((res) => {
        this.doHttpResponse(
          res,
          (data) => {
            success && success(data);
          },
          fail
        );
      });
  }
  private handleError(uri: string, failCallback?) {
    return (error: HttpErrorResponse) => {
      console.dir(error);
      if (failCallback) {
        setTimeout(() => {
          failCallback();
        }, 3000);
      } else {
        this.showAlert('http request ' + uri + ' error ');
      }

      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        console.error(
          'A client-side or network error occurred. Handle it accordingly.'
        );
        this.showAlert('');
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong.
        console.error(
          `Backend returned code ${error.status}, ` + `body was: ${error.error}`
        );
        this.showAlert('');
      }
      // Return an observable with a user-facing error message.
      return throwError('Something bad happened; please try again later.');
    };
  }
  // 
  pollingStart(obj, random: string, domain?: string) {
    let options: any = {
      observe: 'response',
    };

    this.http
      .get(
        this.api.polling + '?random=' + random + (domain ? '&' + domain : ''),
        options
      )
      .pipe(
        catchError(
          this.handleError('polling', () => {
            this.pollingStart(obj, random, domain);
          })
        )
      )
      .subscribe((res: any) => {
        if (!obj.myID) {
          obj.myID = Number(res.headers.get('Uid'));
        }
        // 
        let isNewConnect = res.headers.get('Request-Times') === '1';
        let ret = res.body;
        let isServer = 'rid' in obj;
        this.doHttpResponse(ret, (data) => {
          console.log('data: ', data);
          if (isNewConnect) {
            if (!isServer) {
              data.forEach((d) => {
                this.attachSPT(obj, d, isServer);
              });

              obj.chatList = data || [];
              setTimeout(() => {
                obj.toBottom();
              }, 500);
            }
          } else {
            // 
            data && this.doPolling(data, obj, isServer);
          }
        });
        // -
        this.isLogin && this.pollingStart(obj, random, domain);
      });
  }
  // 
  preview(file, callback) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (e) => {
      callback(e.target.result);
    };
  }
  // 
  uploadFile(fileObj, callback, progressCallback?) {
    const formData = new FormData();
    formData.append('file', fileObj.file);
    let api = 'https://io.imdraw.com:444';
    const req = new HttpRequest('POST', api, formData, {
      reportProgress: true,
    });
    this.http
      .request(req)
      .pipe(
        catchError(
          this.handleError('uploadFile', () => {
            callback(fileObj, true);
            this.showAlert('!');
          })
        )
      )
      .subscribe((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          fileObj.uploadProgress = Math.round(
            (100 * event.loaded) / event.total
          );
          progressCallback && progressCallback(fileObj);
        } else if (event instanceof HttpResponse) {
          let body: any = event.body;
          fileObj.src = api + '/' + body.key;
          fileObj.type === 'file' && (fileObj.src += '?operation=download');
          callback(fileObj);
        }
      });
  }

  debounce(fn, delay = 1000) {
    var timeout;
    return function () {
      clearTimeout(timeout);
      timeout = setTimeout(fn.bind(this, ...arguments), delay);
    };
  }
